import React from "react";
import { ITodo } from "redux/todo/model";
import ToDoItem from "./ToDoItem";

const ToDoList = (todos: ITodo[]) => {
  return (
    <div className="notes-container">
      {todos && todos.map((todo) => <ToDoItem {...todo} />)}
    </div>
  );
};

export default ToDoList;
